@import '_overlay.scss';
.hero {
    text-align: center;
    @include container;
    height: 100vh;
    &::after {
        content: "";
        position: fixed;
        top: 0;
        z-index: -1;
        background: url('../img/heroBackground.jpg');
        background-size: cover;
        height: 100%;
        width: 100%;
    }
    h1 {
        color: white;
        position: relative;
        text-align: center;
        top: 30%;
        font-weight: 200;
        width: 100%;
        margin: 0 auto;
        @include responsive(tablet) {
            width: 100%;
            font-size: 8vw;
        }
        span {
            text-transform: uppercase;
            font-weight: 700;
            display: block;
        }
    }
    .navBar {
        position: absolute;
        bottom: 0%;
        left: 0;
        right: 0;
        .logo {
            display: inline-block;
            @include span(3 of 12);
            margin-left: span(1 of 12);
            img {
                width: 100%;
            }
        }
        @include responsive(tablet) {
            position: fixed;
            bottom: 0;
            left: 0;
            right: 0;
            background: black;
            width: 100%;
            z-index: 100000;
            padding-top: em(10);
        }
        .navMenu {
            display: inline-block;
            position: absolute;
            right: span(1 of 12);
            height: 100%;
        }
        .navButton {
            position: relative;
            cursor: pointer;
            max-width: 100%;
            width: 100%;
            z-index: 20000;
            padding: 10px;
            @include responsive(tablet) {
                height: 55px;
                width: 45px;
            }
            @include responsive(mobile) {
                height: 30px;
                width: 30px;
                padding: 3px;
            }
        }
        .buttonClose {
            display: none;
        }
        .active {
            transition: all ease 0.5s;
            display: block;
        }
    }
}
